"use client";

import React from "react";
import ScheduleForm from "./ScheduleForm";

interface QuickAddModalProps {
  initialDate: Date;
  onClose: () => void;
  onSaved: () => void;
}

export default function QuickAddModal({
  initialDate,
  onClose,
  onSaved,
}: QuickAddModalProps) {
  return (
    <div
      style={{
        position: "fixed",
        inset: 0,
        background: "rgba(0,0,0,0.4)",
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        zIndex: 9999,
        padding: 16,
      }}
      onClick={onClose}
    >
      <div
        style={{
          background: "#fff",
          borderRadius: 8,
          maxWidth: 720,
          width: "100%",
          boxShadow: "0 8px 24px rgba(0,0,0,0.2)",
          padding: 16,
        }}
        onClick={(e) => e.stopPropagation()}
      >
        <div style={{ display: "flex", justifyContent: "flex-end" }}>
          <button
            onClick={onClose}
            aria-label="閉じる"
            style={{ background: "transparent", border: "none", fontSize: 18 }}
          >
            ✕
          </button>
        </div>

        <h3 style={{ marginTop: 0 }}>予定を追加</h3>

        <ScheduleForm
          initialDate={initialDate}
          onSuccess={() => {
            onSaved();
          }}
          onCancel={onClose}
        />
      </div>
    </div>
  );
}
